.CreateNew{cursor:pointer}.CreateNew *{color:#7B7B7B;font-family:'Nanum Gothic', sans-serif;font-weight:700}.CreateNew a{text-decoration:none;color:#7B7B7B}.CreateNew .button-nofill{padding:10px;border:2px solid #FF8181;background-color:white;color:#FF8181;-webkit-transition:all 0.5s;transition:all 0.5s}.CreateNew .button-nofill:hover{background-color:#FF8181;color:white}.CreateNew .button-fill{padding:10px;border:2px solid #FF8181;background-color:#FF8181;color:white;-webkit-transition:all 0.5s;transition:all 0.5s}.CreateNew .button-fill:hover{background-color:white;color:#FF8181}.CreateNew .rect-nofill{color:#FF8181}.CreateNew .rect-fill{background-color:#FF8181;color:white}.CreateNew .highlight{color:#FF8181}.CreateNew .inline{display:inline}.CreateNew .create-menu{border:2px solid #FF8181;position:absolute;display:none;background-color:RGBA(255, 255, 255, 0.8)}.CreateNew .subcontainer{display:flex;justify-content:center;align-items:center;padding:20px 20px}.CreateNew .subcontainer>h4{margin:0 0 0 10px}.CreateNew .subcontainer:hover{background-color:#FFCACA}.CreateNew .show{display:block}

.ShowNoti{}.ShowNoti *{color:#7B7B7B;font-family:'Nanum Gothic', sans-serif;font-weight:700}.ShowNoti a{text-decoration:none;color:#7B7B7B}.ShowNoti .button-nofill{padding:10px;border:2px solid #FF8181;background-color:white;color:#FF8181;-webkit-transition:all 0.5s;transition:all 0.5s}.ShowNoti .button-nofill:hover{background-color:#FF8181;color:white}.ShowNoti .button-fill{padding:10px;border:2px solid #FF8181;background-color:#FF8181;color:white;-webkit-transition:all 0.5s;transition:all 0.5s}.ShowNoti .button-fill:hover{background-color:white;color:#FF8181}.ShowNoti .rect-nofill{color:#FF8181}.ShowNoti .rect-fill{background-color:#FF8181;color:white}.ShowNoti .highlight{color:#FF8181}.ShowNoti .inline{display:inline}.ShowNoti .noti-button{cursor:pointer}.ShowNoti .noti-contents{border:2px solid #FF8181;position:absolute;display:none;background-color:RGBA(255, 255, 255, 0.8)}.ShowNoti .show{display:flex;justify-content:center;align-items:center}

.UserMenu{cursor:pointer;height:100%}.UserMenu *{color:#7B7B7B;font-family:'Nanum Gothic', sans-serif;font-weight:700}.UserMenu a{text-decoration:none;color:#7B7B7B}.UserMenu .button-nofill{padding:10px;border:2px solid #FF8181;background-color:white;color:#FF8181;-webkit-transition:all 0.5s;transition:all 0.5s}.UserMenu .button-nofill:hover{background-color:#FF8181;color:white}.UserMenu .button-fill{padding:10px;border:2px solid #FF8181;background-color:#FF8181;color:white;-webkit-transition:all 0.5s;transition:all 0.5s}.UserMenu .button-fill:hover{background-color:white;color:#FF8181}.UserMenu .rect-nofill{color:#FF8181}.UserMenu .rect-fill{background-color:#FF8181;color:white}.UserMenu .highlight{color:#FF8181}.UserMenu .inline{display:inline}.UserMenu .user-button{height:100%}.UserMenu .user-button>h4{padding:0 50px}.UserMenu .user-menu{border:2px solid #FF8181;position:absolute;display:none;right:0;background-color:RGBA(255, 255, 255, 0.8)}.UserMenu .subcontainer{display:flex;justify-content:center;align-items:center;padding:20px 20px}.UserMenu .subcontainer:hover{background-color:#FFCACA}.UserMenu .show{display:block}.UserMenu h4{margin:0}

.nav{display:flex;border-bottom:2px solid #FF8181;align-items:center}.nav *{color:#7B7B7B;font-family:'Nanum Gothic', sans-serif;font-weight:700}.nav a{text-decoration:none;color:#7B7B7B}.nav .button-nofill{padding:10px;border:2px solid #FF8181;background-color:white;color:#FF8181;-webkit-transition:all 0.5s;transition:all 0.5s}.nav .button-nofill:hover{background-color:#FF8181;color:white}.nav .button-fill{padding:10px;border:2px solid #FF8181;background-color:#FF8181;color:white;-webkit-transition:all 0.5s;transition:all 0.5s}.nav .button-fill:hover{background-color:white;color:#FF8181}.nav .rect-nofill{color:#FF8181}.nav .rect-fill{background-color:#FF8181;color:white}.nav .highlight{color:#FF8181}.nav .inline{display:inline}.nav .icon{width:30px;height:30px}.nav .logo{padding:10px;margin:0}.nav .logo img{display:block;height:50px}.nav .search{display:flex;align-items:center;margin-top:5px;margin-left:10px;height:40px;border:2px solid #FF8181;background-color:#FFEBEB}.nav .search .category{height:40px;width:80px;border:0;background-color:#FFCCCC;text-align-last:center}.nav .search .category:focus{outline:none}.nav .search .category option{text-shadow:0 1px 0 rgba(0,0,0,0.4)}.nav .search .input{flex:1 1;width:30vw;height:40px;margin:0 10px;padding:0;background-color:#FFEBEB;border-width:0}.nav .search .input:focus{outline:none}.nav .search .button{display:flex;align-items:center;justify-content:center;width:40px;height:40px;background-color:#FF8181;border:0;-webkit-transition:all 0.5s;transition:all 0.5s}.nav .search .button:focus{outline:none}.nav .search .button:hover{cursor:pointer}.nav .menu{display:flex;align-items:center;margin-left:auto;height:70px}.nav .menu .center-align{display:flex;align-items:center;height:100%;padding:0 40px}.nav .menu .CreateNew,.nav .menu .ShowNoti{margin-right:20px}

